<template>
    <!-- 使用: 
        默认 size: 12px count: 5 个星星
        普通: <Stars :rank="3.1" :size="12"/>
        10个星星: <Stars :count="10" :rank="3.1" :size="12"/>
    -->
    <div class="stars">
        <i class="star el-icon-star-on"
            :class="[{'star--active': num <= parseFloat(rank)}]"
            v-for="num in count" :key="num"
            :style="{fontSize: size + 'px'}"
        >
            <i class="star-half el-icon-star-on"
                v-if="isHalf && num === parseInt(rank+1)"
                :style="starStyle(num)"
            ></i>
        </i>
    </div>
</template>

<script>
import { parse } from 'path';
export default {
    name: 'Stars',
    data() { 
        return {
        }
    },
    computed: {
        isHalf(){
            // 正整数返回 false, 反之 true
            return parseFloat(this.rank) > 0 && Number.isInteger(parseFloat(this.rank)) ? false : true
        },
    },
    methods: {
        starStyle(num){
            let width_percent = ((parseFloat(this.rank) - parseInt(this.rank)) * 100).toFixed(2) + '%'
            return {
                width: width_percent
            }
        }
        
    },
    props: {
        count: {
            type: [Number, String],
            default: 5
        },
        rank: {
            type: [Number, String],
            default: 0
        },
        size: {
            type: [Number, String],
            default: 12
        }
    },
}
</script>

<style lang="less" scoped>
@import "~@/assets/css/mixin.less";

.stars{
    .flex();
    .star{
        position: relative;
        color: #cbcbcb;
        &--active{
            color: @color-primary;
        }
        &-half{
            position: absolute;
            left: 0;
            top: 0;
            overflow: hidden;
            color: @color-primary;
        }
    }
}
</style>