<template>
    <button class="rating_btn"
        @click="handleClick"
    >
        <slot></slot>
    </button>
</template>

<script>
export default {
    name: 'RatingButton',
    data() {
        return {

        }
    },
    methods: {
        handleClick(event){
            this.$emit('click', event)
        }
    },
}
</script>

<style lang="less" scoped>
.rating_btn{
    font-size: 12px;
    line-height: 1;
    padding: 6px 12px;
    background-color: #e8f2fb;
    color: #777;
    border-radius: 4px;
    &.active{
        background-color: #22a6f2;
        color: #fff;
    }
}
</style>