<template>
    <div class="controller">
        <!-- add -->
        <span class="controller-btn add" @click="add">
            <i class="el-icon-circle-plus"></i>
        </span>

        <transition
            @before-enter="before"
            @enter="slideLeft"
        >
            <span class="controller-count" v-if="count > 0">{{count}}</span>
        </transition>
        <transition
            @before-enter="before"
            @enter="slideLeft"
        >
            <!-- remove -->
            <span class="controller-btn remove" v-if="count > 0" @click="remove">
                <i class="el-icon-remove"></i>
            </span>
        </transition>
        
    </div>
</template>

<script>
import velocity from 'velocity-animate'

export default {
    name: 'ShoppingCartButtons',
    data() {
        return {
            count: 0
        }
    },
    methods: {
        before(el){
            el.style.opacity = 0
        },
        slideLeft(el, done){
            velocity(el, {opacity: 1}, {duration: 300}, {complete: done})
        },
        add(){
            this.count++
            this.$emit('addGoods')
        },
        remove(){
            this.count > 0 && this.count--
            this.$emit('removeGoods')
        }
    },
}
</script>

<style lang="less" scoped>
@import "~@/assets/css/mixin.less";

.controller{
    .flex-sb-center();
    flex-direction: row-reverse;
    width: 60px;
    &-btn{
        color: #50d25b;
        font-size: 20px;
    }
}
</style>