
// 吸顶效果
// 滚动条滚动到一定位置后, 为绑定的元素添加 .fixed 类,并设置 top 位置
// 使用: <div v-fixed="50"><div/> 其中 50 为 top, 即固定后的 top 值.
let scrollCallback
let offsetTop = 0 // 用于存储吸顶前的 el.offsetTop
export default {
    bind(el, binding){
        let fixedTop = binding.value
        scrollCallback = () => {
            // 吸顶前的 el.offsetTop
            if(offsetTop === 0){
                offsetTop = el.offsetTop
            }

            if(document.querySelector('main.main').scrollTop > offsetTop - fixedTop
                && el.offsetTop !== fixedTop){ // 元素固定
                    el.style.top = fixedTop + 'px'
                    el.classList.add('fixed')
            }
            if(document.querySelector('main.main').scrollTop < offsetTop - fixedTop - el.offsetHeight
                && el.offsetTop === fixedTop){ // 取消元素固定
                    el.style.top = ''
                    el.classList.remove('fixed')
            }
        }
        window.addEventListener("scroll", scrollCallback, true)
    },
    unbind(){
        // 销毁
        window.removeEventListener("scroll", scrollCallback)
    }
}