<template>
<!-- 
    引用:
    <ImageList :urls="rating.pictures"/>
    10px 的间距:
    <ImageList :urls="rating.pictures" :gap="10"/>
-->
    <div class="image_list" ref="wrap" v-if="urls.length > 0">
        <div class="image_list-item"
            :style="[imageStyle]"
            v-for="(url, index) in urls" :key="index"
        >
            <img :src="url">
        </div>
        <div class="empty" :style="imageStyle"></div>
    </div>
</template>

<script>
export default {
    name: 'ImageList',
    props: {
        urls: {
            type: Array,
            default: []
        },
        gap: { // 间距 单位 px
            type: [Number, String],
            default: 4
        }
    },
    data() {
        return {
            imageWidth: 0
        }
    },
    computed: {
        imageStyle(){
            return {
                width: this.imageWidth + 'px',
                marginTop: this.gap + 'px'
            }
        },
    },
    methods: {
        _initImageWidth(){
            let urlsLen = this.urls.length
            if(urlsLen === 0) return
            let columns = ''
            if(urlsLen <=2){
                columns = '2列'
            }else if(urlsLen === 3){
                columns = '3列'
            }else if(urlsLen === 4){
                columns = '2列'
            }else if(urlsLen >= 5){
                columns = '3列'
            }
            switch(columns){
                case '2列': this.imageWidth = (this.$refs.wrap.offsetWidth - this.gap) / 2; break;
                case '3列': this.imageWidth = (this.$refs.wrap.offsetWidth - this.gap * 2) / 3; break;
            }
        }
    },
    mounted() {
        this._initImageWidth()
    },
    
}
</script>

<style lang="less" scoped>
@import "~@/assets/css/mixin.less";

.image_list{
    .flex();
    justify-content: space-between;
    flex-wrap: wrap;
    &-item{
        &>img{
            width: 100%;
            display: block;
        }
    }
    .empty{
        visibility: hidden;
    }
}
</style>