<template>
    <div class="store_ordering">
        <div class="store_ordering-container">
            <!-- //* aside -->
            <aside class="store_ordering-aside"
                :style="heightStyle"
            >
                <nav class="store_ordering-aside-nav">
                    <span class="store_ordering-aside-nav-item"
                        v-for="(item, index) in goods" :key="index"
                        :class="{'store_ordering-aside-nav-item--active': currentNavIndex === index}"
                        @click="tabNavs(item.name, index)"
                    >{{item.name}}</span>
                </nav>
            </aside>
            <!-- //* main -->
            <section class="store_ordering-main"
                :style="heightStyle"
            >
                <ul class="store_ordering-list" v-if="goods.length > 0">
                    <li class="store_ordering-list-item"
                        v-for="(item, index) in foods" :key="index"
                    >
                        <router-link class="store_ordering-list-item-link"
                            to=""
                            v-if="item"
                        >
                            <div class="item-image" :style="{'background-image': `url(${item.icon})`}"></div>
                            <div class="item-content">
                                <div class="item-content-header">
                                    <p class="item-content-header-title">{{item.name}}</p>
                                    <p class="item-content-header-description">{{item.description}}</p>
                                    <p class="item-content-header-text">
                                        <span>月售{{item.sellCount}}份</span>
                                        <span>好评率{{item.rating}}</span>
                                    </p>
                                </div>
                                <div class="item-content-footer">
                                    <span class="item-content-footer-price">&yen;{{item.price}}</span>
                                    <ShoppingCartButtons @addGoods="addGoods" @removeGoods="removeGoods"/>
                                </div>
                            </div>
                        </router-link>
                    </li>
                </ul>
            </section>
        </div>
        <shoppingBar/>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import shoppingBar from '../base/ShoppingBar.vue'
import ShoppingCartButtons from '../base/ShoppingCartButtons.vue'

export default {
    name: 'StoreDetailsOrdering',
    components: {
        shoppingBar,
        ShoppingCartButtons
    },
    data() {
        return {
            offsetHeights: {
                body: 0,
                sotreHeader: 0,
                mainNav: 0,
                shoppingBar: 0,
            },
            currentNavIndex: 0,
        }
    },
    computed: {
        ...mapState('store', {
            goods: state => state.goods
        }),
        heightStyle(){
            return {
                maxHeight: this.offsetHeights.body
                    - this.offsetHeights.sotreHeader
                    - this.offsetHeights.mainNav
                    - this.offsetHeights.shoppingBar + 'px'
            }
        },
        foods(){
            return this.goods[this.currentNavIndex].foods
        },
    },
    methods: {
        tabNavs(name, index){
            this.currentNavIndex = index
        },
        addGoods(){
            console.log('add')
        },
        removeGoods(){
            console.log('remove')
        },
    },
    mounted() {
        this.offsetHeights.body = document.body.offsetHeight
        this.offsetHeights.sotreHeader = document.querySelector('.store-header').offsetHeight
        this.offsetHeights.mainNav = document.querySelector('.store-details-main-nav').offsetHeight
        this.offsetHeights.shoppingBar = document.querySelector('.shopping_bar').offsetHeight
    },
}
</script>

<style lang="less">
@import "~@/assets/css/mixin.less";

.store_ordering{
    &-container{
        display: flex;
        // 底部购物车的高度
        padding-bottom: 50px;
    }
    // * aside
    &-aside{
        flex-shrink: 0;
        width: 80px;
        height: 100%;
        background-color: @bg-base;
        overflow-y: scroll;
        &::-webkit-scrollbar  {
            display: none;
        }
        &-nav{
            /* 在webkit内核下将滚动条隐藏，Firefox下采用容器覆盖的机制 */
            height: 100%;
            &-item{
                .flex-center();
                height: 52px;
                background-color: #f3f5f7;
                color: #000;
                .font-size-rem(@font-size-sm);
                border-bottom: 1px solid #e7e9eb;
                &--active{
                    background-color: @bg-white;
                    font-weight: bold;
                }
            }
        }
    }
    // * main
    &-main{
        background-color: @bg-white;
        flex-grow: 1;
        overflow-y: scroll;
        .padding-horizontal(18px);
        .store_ordering-list{
            &::-webkit-scrollbar  {
                display: none;
            }
            &-item{ // .store_ordering-list-item
                border-bottom: 1px solid #f3f3f4;
                &-link{
                    .padding-vertical(18px);
                    @imageHeight: 55px;
                    .flex();
                    .item-image{
                        flex-shrink: 0;
                        .image-bg(@imageHeight);
                    }
                    .item-content{
                        .margin-left-rem(@margin-base);
                        .font-size-rem(12px);
                        flex-grow: 1;
                        &-header{
                            height: @imageHeight;
                            .flex-column();
                            justify-content: space-between;
                            &-title{
                                .font-size-rem(14px);
                                color: #000;
                                font-weight: bold;
                            }
                            &-description{
                                overflow: hidden;
                                line-height: 1em;
                                height: 1em;
                            }
                            &-text{
                                &>span{
                                    margin-left: 10px;
                                    &:first-of-type{
                                        margin-left: 0;
                                    }
                                }
                            }
                        }
                        &-footer{
                            .flex-sb-center();
                            .margin-top-rem(@margin-lg);
                            &-price{
                                color: #f20b15;
                                .font-size-rem(@font-size-base, bold);
                            }
                        }
                    }
                }
            }
        }
        .shopping_bar{
            // position: fixed;
            // bottom: 0;
            // width: 100%;
            // background-color: rgba(0, 0, 0, .9);
        }
    }
}
</style>