<template>
    <section class="store_rating">
        <!-- //* header -->
        <div class="store_rating-header">
            <div class="store_rating-header-rank store_rating-header-item">{{seller.storeRank}}</div>
            <div class="store_rating-header-rank_star">
                <span class="item-text">商家评分</span>
                <Stars :rank="seller.storeRank"/>
            </div>
            <div class="store_rating-header-item">
                <span class="item-text">味道</span>
                <span class="item-num">{{seller.storeRank}}</span>
            </div>
            <div class="store_rating-header-item">
                <span class="item-text">包装</span>
                <span class="item-num">{{seller.storeRank}}</span>
            </div>
            <div class="store_rating-header-item">
                <span class="item-text">配送</span>
                <span class="item-num">{{seller.storeRank}}</span>
            </div>
        </div>
        <!-- header end -->
        <!-- //* tabs -->
        <div class="store_rating-tabs">
            <RatingButton class="store_rating-tabs-item"
                :class="{active: label === currentTab}"
                v-for="(label, index) in ratingTabs" :key="index"
                @click="tabRatings(label)"
            >{{label}}</RatingButton>
        </div>
        <!-- tabs end -->
        <!-- //* rating -->
        <div class="store_rating-rating">
            <ul class="rating_list">
                <li class="rating_list-item"
                    v-for="(rating, index) in filterRatings" :key="index"
                >
                    <div class="item-image"
                        :style="{backgroundImage: `url(${rating.avatar})`}"
                    ></div>
                    <div class="item-content">
                        <!-- header -->
                        <div class="item-content-header">
                            <div class="flex-column-sb">
                                <span class="item-content-header-username">{{rating.username}}</span>
                                <div class="item-content-header-stars_wrap">
                                    <Stars :rank="rating.score"/>
                                    <span class="satisfaction">{{rating.score | satisfaction}}</span>
                                </div>
                            </div>
                            <span class="item-content-header-ratetime">{{rating.rateTime | rateTimeFormat}}</span>
                        </div>
                        <!-- header end -->
                        <!-- 评论 -->
                        <p class="item-content-text">{{rating.text}}</p>
                        <!-- 九宫图 -->
                        <ImageList :urls="rating.pictures"/>
                        <div class="item-content-recommend_list">
                            <RatingButton class="item-content-recommend_list-item"
                                v-for="(item, index) in rating.recommend" :key="index"
                            >{{item}}</RatingButton>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
        <!-- rating end -->
    </section >
</template>

<script>
import { mapState } from 'vuex'
import ImageList from '../base/ImageList.vue'
import Stars from '../base/Stars.vue'
import RatingButton from '../base/RatingButton.vue'
import { compareObject } from '../../utils/util.js'

export default {
    name: 'StoreDetailsRating',
    components: {
        ImageList,
        Stars,
        RatingButton
    },
    data() {
        return {
            ratingTabs: ['全部', '最新', '好评', '差评', '有图'],
            currentTab: '全部'
        }
    },
    computed: {
        ...mapState('store', {
            seller: state => state.seller,
            ratings: state => state.ratings
        }),
        filterRatings(){
            let type = this.currentTab
            switch(type){
                case '全部': return this.ratings
                case '最新': return [...this.ratings].sort(compareObject('rateTime', -1))
                case '好评': return this.ratings.filter(rating => rating.score >= 3)
                case '差评': return this.ratings.filter(rating => rating.score < 3)
                case '有图': return this.ratings.filter(rating => rating.pictures.length > 0)
                default: return this.ratings
            }
        },
    },
    methods: {
        tabRatings(label){
            this.currentTab = label
        }
    },
    filters: {
        rateTimeFormat(value){
            let date = new Date()
            date.setTime(value)
            return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
        },
        satisfaction(value){
            if(value < 3){
                return '吐槽'
            }else if(value >= 3 && value < 5){
                return '满意'
            }else if(value = 5){
                return '超赞'
            }
        }
    }
}
</script>

<style lang="less">
@import "~@/assets/css/mixin.less";

.store_rating{
    &-header,
    &-tabs,
    &-rating{
        .padding-horizontal(@padding-base);
        background-color: @bg-white;
    }
    // header
    &-header{
        height: 80px;
        .flex-center();
        &>div{
            flex-grow: 1;
        }
        &-rank{
            .font-size-rem(40px);
            color: #dd5145;
        }
        &-rank_star{
            .flex-column-center-sb();
            height: 36px;
            font-size: 12px;
        }
        &-item{
            .flex-column();
            height: 50px;
            justify-content: space-between;
            align-items: center;
            .item-text{
                font-size: 12px;
            }
            .item-num{
                font-size: 24px;
            }
        }
    }
    // tabs
    &-tabs{
        margin-top: 5px;
        .padding-vertical(15px);
        border-bottom: 1px solid @bg-base;
        &-item{
            margin-left: 5px;
            &:first-of-type{
                margin-left: 0;
            }
        }
    }
    // ratings
    &-rating{
        height: 100%;
        overflow: hidden;
        .rating_list{
            &-item{
                @header-height: 34px;
                .padding-vertical(@padding-lg);
                border-top: 2px solid @bg-base;
                &:first-of-type{
                    border-top: 0;
                }
                .flex();
                .item-image{
                    flex-shrink: 0;
                    .image-bg-circle(@header-height);
                }
                .item-content{
                    flex-grow: 1;
                    .margin-left-rem(@margin-base);
                    &-header{
                        height: @header-height;
                        .flex();
                        justify-content: space-between;
                        &-stars_wrap{
                            .flex();
                            // margin-top: 10px;
                            align-items: center;
                            .satisfaction{
                                margin-left: 8px;
                                font-size: 12px;
                                color: @color-primary;
                            }
                        }
                        &-ratetime{
                            font-size: 12px;
                            color: #ccc;

                        }
                    }
                    &-text{
                        .font-size-rem(@font-size-base);
                        color: #000;
                        .margin-top-rem(@margin-base);
                    }
                    &-recommend_list{
                        .flex();
                        flex-wrap: wrap;
                        &-item{
                            margin-left: 6px;
                            margin-top: 6px;
                            &:first-of-type{
                                margin-left: 0;
                            }
                        }
                    }
                }
            }
        }
    }
}
</style>