                <p>配送费{{seller.deliveryPrice}}</p>
            </div>
        </div>

        <!-- 商家服务 -->
        <div class="store_info-module">
            <div class="store_info-module-head">商家服务</div>
            <div class="store_info-module-content store_info-service">
                <div class="store_info-service-item"
                    v-for="(service, index) in seller.storeServices" :key="index"
                >
                    <ServiceLabel :type="service.type"/>
                    <p class="item-description">{{service.description}}</p>
                </div>
            </div>
        </div>

        <!-- 商家实景 -->
        <div class="store_info-module">
            <div class="store_info-module-head">商家实景</div>
            <div class="store_info-module-content store_info-live_action">
                <div class="store_info-live_action-list">
                    <div class="list-item"
                        :style="{backgroundImage: `url(${url})`}"
                        v-for="(url, index) in seller.pics" :key="index"
                    ></div>
                </div>
            </div>
        </div>

        <!-- 商家信息 -->
        <div class="store_info-module">
            <div class="store_info-module-head">商家信息</div>
            <div class="store_info-module-content store_info-seller_info">
                <!-- 简介 -->
                <div class="store_info-seller_info-item" v-if="seller.infos[0]">
                    <div class="store_info-seller_info-item-label">简介</div>
                    <div class="store_info-seller_info-item-details">{{seller.infos[0]}}</div>
                </div>
                <div class="store_info-seller_info-else" v-else>暂无简介</div>

                <!-- 品类 -->
                <div class="store_info-seller_info-item">
                    <div class="store_info-seller_info-item-label">品类</div>
                    <div class="store_info-seller_info-item-details">{{seller.infos[1]}}</div>
                </div>

                <!-- 商家电话 -->
                <div class="store_info-seller_info-item">
                    <div class="store_info-seller_info-item-label">商家电话</div>
                    <div class="store_info-seller_info-item-details">
                        <span>{{seller.infos[2]}}</span>
                        <i class="el-icon-arrow-right"></i>
                    </div>
                </div>

                <!-- 地址 -->
                <div class="store_info-seller_info-item">
                    <div class="store_info-seller_info-item-label">地址</div>
                    <div class="store_info-seller_info-item-details">{{seller.infos[3]}}</div>
                </div>

                <!-- 营业时间 -->
                <div class="store_info-seller_info-item">
                    <div class="store_info-seller_info-item-label">营业时间</div>
                    <div class="store_info-seller_info-item-details">{{seller.infos[4]}}</div>
                </div>
            </div>
        </div>

        <!-- 营业资质 -->
        <div class="store_info-module">
            <div class="store_info-module-head">
                <div class="flex-sb-center">
                    <span>营业资质</span>
                    <i class="el-icon-arrow-right"></i>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import ServiceLabel from '../base/ServiceLabel.vue'

import { mapState } from 'vuex'

export default {
    name: 'StoreDetailsInfo',
    components: {
        ServiceLabel
    },
    data() {
        return {

        }
    },
    computed: {
        ...mapState('store', {
            seller: state => state.seller
        })
    },
}
</script>

<style lang="less">
@import "~@/assets/css/mixin.less";

.store_info{
    &-module{
        // margin-top: 10px;
        .margin-bottom-rem(@margin-base);
        background-color: @bg-white;
        .padding-vertical(@padding-lg);
        .padding-horizontal(@padding-lg);
        &-head{
            .font-size-rem(@font-size-root, bold);
            line-height: 1;
            color: #000;
        }
        &-content{
            font-size: 12px;
            color: #666;
        }
    }
    // 配送信息
    &-deliver_info{
        &>p{
            .margin-top-rem(@margin-base);
        }
    }
    // 商家服务
    &-service{
        &-item{
