                .image-bg(20.8vw);
            }
        }
    }
    // 商家信息
    &-seller_info{
        &-item{
            .flex-sb-center();
            .padding-vertical(@padding-base);
            border-top: 1px solid #e3e3e3;
            &:first-of-type{
                border-top: 0;
            }
            &-label{
                flex-shrink: 0;
                flex-basis: 80px;
                .font-size-rem(@font-size-base);
                color: #000;
            }
            &-details{
                margin-left: 10px;
                flex-grow: 1;
                color: #666;
                text-align: right;
            }
        }
        &-else{
            .padding-vertical(@padding-base);
        }
    }
