<template>
    <Layout closeTabbar>
        <template v-slot:header>
            <!-- //* header -->
            <section class="store-header" ref="headerBar">
                <i class="iconfont icon-back-m store-header-active_icon" @click="$router.go(-1)"></i>
                <h1 class="store-header-title">{{seller.storeName}}</h1>
                <i class="el-icon-search store-header-active_icon"></i>
                <i class="el-icon-share"></i>
            </section>
        </template>
        <!-- //* store details -->
        <div class="store-details">
            <!-- //* header -->
            <StoreDetailsHeader/>
            <section class="store-details-main">
                <!-- //* nav -->
                <div class="store-details-main-nav" ref="nav" v-fixed="44">
                    <span class="store-details-main-nav-item"
                        :class="{'store-details-main-nav-item--active': currentTab === tab}"
                        v-for="(tab, index) in tabs" :key="index"
                        @click="currentTab = tab"
                    >{{tab}}</span>
                </div>
                <!-- //* 点菜 评论 商家 -->
                <component :is="currentComponent"></component>
            </section>
        </div>
    </Layout>
</template>

<script>
import Layout from '../layout/Layout.vue'
import StoreDetailsHeader from './StoreDetailsHeader.vue'
import StoreDetailsOrdering from './StoreDetailsOrdering.vue'
import StoreDetailsRating from './StoreDetailsRating.vue'
import StoreDetailsInfo from './StoreDetailsInfo.vue'
import { mapState, mapActions } from 'vuex'

// 吸顶效果
import fixedDirective from '../../directives/fixed.js'

export default {
    name: 'Store',
    components: {
        Layout,
        StoreDetailsHeader,
        StoreDetailsOrdering,
        StoreDetailsRating,
        StoreDetailsInfo,
    },
    data() {
        return {
            currentTab: '点菜',
            tabs: ['点菜', '评价', '商家'],
            currentNavIndex: 0,
        }
    },
    computed: {
        ...mapState('store', {
            seller: state => state.seller
        }),
        currentComponent(){
            switch(this.currentTab){
                case '点菜': return 'StoreDetailsOrdering'
                case '评价': return 'StoreDetailsRating'
                case '商家': return 'StoreDetailsInfo'
            }
        },
    },
    methods: {
        ...mapActions('store', [
            'requestStoreData'
        ]),
    },
    directives: {
        fixed: fixedDirective
    },
    beforeRouteEnter (to, from, next) {
        next(vm => {
            vm.requestStoreData(to.params.id)
        })
    },
}
</script>

<style lang="less">
@import "~@/assets/css/mixin.less";

// header
.store-header{
    position: relative;
    z-index: @zindex-tooltip;
    @item-margin: 15px;
    .flex-sb-center();
    .padding-vertical(@padding-base);
    .padding-horizontal(@padding-base);
    background-color: @bg-white;
    border-bottom: 1px solid @border-color-divition;
    &-title{
        .margin-left-rem(@item-margin);
        .flex-grow(1);
        color: #000;
    }
    &-active_icon{
        color: #328bf6;
        font-size: 20px;
    }
    .el-icon-share{
        .margin-left-rem(@item-margin);
    }
}
// store
.store-details{
    &-main{
        // * nav
        &-nav{
            background-color: @bg-white;
            &.fixed{
                position: fixed;
                // top: 44px;
                left: 0;
                width: 100%;
                z-index: @zindex-tooltip;
                background-color: @bg-white;
            }
            .flex-sb-center();
            .padding-horizontal(30px);
            border-bottom: 1px solid #ddd;
            &-item{
                position: relative;
                text-align: center;
                .font-size-rem(@font-size-base);
                .padding-vertical(12px);
                &--active{
                    font-weight: bold;
                    &::after{
                        content: '';
                        position: absolute;
                        left: 0;
                        bottom: 0;
                        width: 100%;
                        height: 2px;
                        background-color: @color-primary;
                    }
                }
            }
        }
    }
}
</style>