<template>
    <span class="service_label">
        {{type | label}}
    </span>
</template>

<script>
export default {
    name: 'ServiceLabel',
    props: {
        /**
            1: 商家导致订单取消, 赔付代金券
            2: 该商户食品安全已由国泰产险承担, 食品安全有保障
         */
        type: {
            type: Number,
            required: true,
            validator: value => [1, 2].indexOf(value) !== -1
        }
    },
    data() {
        return {

        }
    },
    filters: {
        label(value){
            switch(value){
                case 1:
                case '1': return '陪'
                case 2:
                case '2': return '保'
            }
        }
    },
}
</script>

<style lang="less" scoped>
.service_label{
    border: 1px solid #cbcbcb;
    padding: 0 2px;
    color: #999;
}
</style>