<template>
    <div class="store_details-header">
        <!-- 背景图 -->
        <div class="store_details-header-bg" :style="{backgroundImage: `url(${seller.storeBg})`}">
            <div class="store_details-header-bg-logo" :style="{backgroundImage: `url(${seller.storeImage})`}"></div>
        </div>
        <!-- 背景图 end -->
        <section class="store_details-header-info">
            <p class="title">{{seller.storeName}}</p>
            <p class="text text-center">评价{{seller.storeRank}} 月售{{seller.sellCount}}单 商家配送约{{seller.deliveryTime}}</p>
            <p class="text text-activities" v-if="seller.activities">
                <ActivityLabel :type="seller.activities[0].type"/>
                <span class="text-module1">{{seller.activities[0].description}}</span>
                <span class="popun-btn" @click="activityPopup = true">{{seller.activities.length}}个优惠<i class="el-icon-caret-bottom"></i></span>
            </p>
            <p class="text">公告: {{seller.bulletin}}</p>
        </section>
        <!-- activity 弹窗 -->
        <transition
            @enter="slideUp"
            @leave="slideDonw"
        >
            <div class="popup-activity"
                v-if="activityPopup"
                @click.self="activityPopup = false"
            >
                <div class="popup-activity-inner">
                    <!-- close -->
                    <span class="popup-close" @click="activityPopup = false">
                        <i class="el-icon-close"></i>
                    </span>
                    <div class="popup-activity-header">优惠活动</div>
                    <!-- 活动 -->
                    <ul class="popup-activity-activities">
                        <li class="popup-activity-activities-item text-activities"
                            v-for="(activity, index) in seller.activities" :key="index"
                        >
                            <ActivityLabel :type="activity.type"/>
                            <span class="activity-label">{{activity.description}}</span>
                        </li>
                    </ul>
                </div>
            </div>
        </transition>
        <!-- activity 弹窗 end -->
    </div>
</template>

<script>
import ActivityLabel from '../base/ActivityLabel.vue'
import { mapState } from 'vuex'
import velocity from 'velocity-animate'

export default {
    name: 'StoreDetailsHeader',
    components: {
        ActivityLabel
    },
    data() {
        return {
            activityPopup: false,
        }
    },
    computed: {
        ...mapState('store', {
            seller: state => state.seller
        }),
    },
    methods: {
        beforeEnter(el){
            el.style.display = 'none'
        },
        slideUp(el, done){
            velocity(el.querySelector('.popup-activity-inner'), 'slideDown', { duration: 300 }, { complete: done })
        },
        slideDonw(el, done){
            velocity(el, { opacity: 0 } )
            velocity(el.querySelector('.popup-activity-inner'), 'slideUp', { duration: 300 })
            velocity(el, { display: 'none' }, { complete: done } )
        },
    },
}
</script>

<style lang="less">
@import "~@/assets/css/mixin.less";

// mixins
.text-activities(){
    font-size: 12px;
    .flex();
    .align-items(center);
    .activity_label{
        .flex-shrink(0);
    }
}

.store_details-header{
    @margin: 15px;
    background-color: @bg-white;
    // 背景图
    &-bg{
        .image-bg(100%, 120px);
        position: relative;
        &-logo{
            @size: 80px;
            position: absolute;
            left: 50%;
            margin-left: -@size / 2;
            bottom: -@margin;
            .image-bg(@size);
            z-index: 10;
        }
    }
    // info
    &-info{
        margin-top: @margin;
        .padding-vertical(@padding-lg);
        .padding-horizontal(30px);
        .title{
            text-align: center;
            .font-size-rem(@font-size-title, bold);
            color: #000;
        }
        .text{
            .font-size-rem(@font-size-sm);
            .margin-top-rem(@margin-base);
            &-module1{
                color: #333;
                margin-left: 10px;
                .ellipsis();
                .flex-grow(1);
            }
        }
        .text-center{
            text-align: center;

        }
        .text-activities{
            .text-activities();
        }
        .popun-btn{
            .flex-shrink(0);
            margin-left: 10px;
        }
    }
}
// activity 弹窗
.popup-activity{
    .popup();
    top: 0;
    // top: 50px;
    &-inner{
        // height: 220px;
        bottom: 0;
        .padding-horizontal(30px);
        .padding-vertical(20px);
    }
    // 关闭按钮
    .popup-close{
        position: absolute;
        right: 20px;
        top: 20px;
        font-size: 20px;
    }
    &-header{
        text-align: center;
        color: #000;
        .font-size-rem(@font-size-title, bold);
    }
    &-activities{
        &-item{
            display: flex;
            align-items: center;
            .margin-top-rem(@margin-base);
            .font-size-rem(@font-size-base);
            .activity-label{
                margin-left: 10px;
            }
        }
    }
}
</style>