<template>
    <span class="activity_label"
        :style="{backgroundColor: data[type].bgColor}"
    >
        {{data[type].label}}
    </span>
</template>

<script>
export default {
    name: 'ActivityLabel',
    props: {
        type: {
            type: Number,
            required: true,
            validator: value => [1, 2, 3, 4, 5, 6].indexOf(value) !== -1
        }
    },
    data() {
        return {
            data: {
                1: {
                    label: '满减',
                    bgColor: '#f576ab'
                },
                2: {
                    label: '会员',
                    bgColor: '#f2b47e'
                },
                3: {
                    label: '特价',
                    bgColor: '#f773ee'
                },
                4: {
                    label: '新客',
                    bgColor: '#6cf48a'
                },
                5: {
                    label: '首单',
                    bgColor: '#a2f481'
                },
                6: {
                    label: '领券',
                    bgColor: '#f2ba7e'
                },
            }
        }
    },
    computed: {
        label(){
            switch(this.type){
                case 1: return '满减'
                case 2: return '满减'
                case 3: return '满减'
                case 4: return '满减'
                case 5: return '满减'
                case 6: return '满减'
            }
        }
    },
}
</script>

<style lang="less" scoped>
.activity_label{
    font-size: 12px;
    padding: 0 1px;
    text-align: center;
    color: #fff;
    white-space: nowrap;
}
</style>